import React, { useEffect, useRef, useState } from 'react';

const SpringCode = (props) => {

    const [scrollY, setScrollY] = useState(0);
    function logit() {
        setScrollY(window.pageYOffset);

        // 
        let elementHeight = document.getElementsByClassName('navMenu')[0].scrollHeight +
            document.getElementsByClassName('navMenu2')[0].scrollHeight;
        let testLeft = document.getElementsByClassName('navMenu2')[0].getBoundingClientRect().left;
        let testdis = document.getElementsByClassName('common_style')[0].getBoundingClientRect().left
            - document.getElementsByClassName('lblocknav_container')[0].getBoundingClientRect().left;
        // console.log(window.innerWidth);

        //    +  
        if (document.documentElement.scrollTop > elementHeight && window.innerWidth < 1024) {
            blockRef.current[0].style.position = 'fixed';
            blockRef.current[0].style.left = document.getElementsByClassName('navMenu2')[0].getBoundingClientRect().left + "px";
            blockRef.current[0].style.maxWidth = '420px';
            blockRef.current[1].style.left = '25%';
            blockRef.current[0].style.display === 'inline-block' ?
                document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none' :
                document.getElementsByClassName('lblocknav_btn')[0].style.display = 'inline-block';
        }
        //   
        else if (document.documentElement.scrollTop > elementHeight && window.innerWidth > 1023) {
            //    ,  ,  
            blockRef.current[0].style.position = 'fixed';
            blockRef.current[0].style.display = 'inline-block';
            blockRef.current[0].style.maxWidth = testdis + "px";
            blockRef.current[0].style.left = document.getElementsByClassName('navMenu2')[0].getBoundingClientRect().left + "px";
            //       
            blockRef.current[1].style.left = '25%';
            document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none';
        }
        //       + 
        else if (document.documentElement.scrollTop < elementHeight && window.innerWidth > 1023) {
            blockRef.current[0].style.position = 'relative';
            blockRef.current[0].style.left = '0px';
            blockRef.current[0].style.display = 'inline-block';
            blockRef.current[1].style.left = null;
        }

    }

    const blockRef = useRef([]);

    useEffect(() => {
        function watchScroll() {
            window.addEventListener("scroll", logit);
        }
        watchScroll();
        return () => {
            window.removeEventListener("scroll", logit);
        };
    });

    return (
        <>
            <button className="lblocknav_btn" onClick={() => {
                blockRef.current[0].style.display === 'inline-block' ?
                    blockRef.current[0].style.display = 'none' :
                    blockRef.current[0].style.display = 'inline-block';
                document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none';
            }}>  </button>
            <div className="lblocknav_container" ref={(elem) => (blockRef.current[0] = elem)}>
                <div className="lblocknav">
                    <span className="mtitle">
                        <a href="#" className="col_b"> <b> Eclipse Bean Xml Injection </b> </a>
                        <button className="lblocknav_btn2" onClick={() => {
                            document.getElementsByClassName('lblocknav_btn')[0].style.display = 'block';
                            document.getElementsByClassName('lblocknav_container')[0].style.display = 'none';
                        }}>  </button>
                    </span>
                    <p className="hyperlink">
                        <details open>
                            <summary>    </summary>
                            <a href="# applicationContext.xml">  applicationContext.xml </a>
                            <a href="#cmd  "> cmd   </a>
                            <a href="#">  </a>
                            <a href="#xml  "> xml   </a>
                        </details>
                    </p>
                </div>
            </div>
            {/* <p> <img src={process.env.PUBLIC_URL + '/img/SpringExCode1/Directory_1.PNG'} alt='' /> </p> */}
            <div className="common_style" ref={(elem) => (blockRef.current[1] = elem)} >
                <span className="lblock">
                    <span className="mtitle">    </span>
                    <p> <small>  </small> </p>
                    <span className="mblock">
                        <span className="stitle"> <a name=" applicationContext.xml">   applicationContext.xml </a> </span>
                        <p> <small>  applicationContext.xml  </small> </p>
                        <span className="sblock">
                            <p className="sstitle" ><i> &lt; bean &gt; </i>  </p>
                            <p>   . </p>
                            <p> <small> &lt; bean class="." id="" init-method="" destroy-method=""
                                lazy-init="false(default)" scope="singleton(default)" /&gt; </small> </p>
                            <p> init-method="() :       </p>
                            <p> destroy-methode="() :       </p>
                            <p> lazy-init="false(default) :        </p>
                            <p> scope="singleton(default) :       </p>
                            <span className="lblock">
                                <p className="sstitle" ><i> &lt; constructor-arg ref="" &gt; </i>  </p>
                                <p><small> &lt; constructor-arg ref="sw" / &gt;  </small></p>
                                <p><small> &lt; bean class="coupling.AppleWatch" id="sw" lazy-init="true" / &gt;  </small></p>
                            </span>
                        </span>
                        <span className="sblock">
                            <p className="sstitle" ><i> &lt; import &gt; </i>  </p>
                            <p><small> &lt; import resource="~~.xml" / &gt;  </small></p>
                            <p> resource :  xml ( xml  )  </p>
                            <p> &lt;  &gt;  </p>
                        </span>
                        <span className="sblock">
                            <p className="sstitle" ><i> &lt;   &gt; </i>  </p>
                            <p> applicationContext.xml   - open with - Spring Config Editor  appliactionContext.xml 
                                    Namespaces context  
                            </p>
                            <p>    xmlns:context="http://www.springframework.org/schema/context"   </p>
                            <p> <i>  </i> </p>
                            <p> &lt; context:component-scan base-package="" &gt;  </p>
                            <p> <i>  </i> </p>
                            <p> @Componet("")    </p>

                        </span>
                        {/* <span className="sblock">
                            <p className="sstitle" ><i> &lt; &gt; </i>  </p>
                        </span> */}
                    </span>
                    {/*  applicationContext.xml  */}
                    <span className="mblock">
                        <span className="stitle">
                            <a name="cmd  "> cmd   </a>
                        </span>
                        <span className="sblock">
                            <p className="sstitle" ><i>1.</i>  </p>
                            <p> cmd - cd  - gradlew.bat build  - cd build - cd libs - java -jar -0.0.1-SNAPSHOT.jar</p>
                        </span>
                    </span>
                    <span className="mblock">
                        <span className="stitle">
                            <a name="">  </a>
                        </span>
                        <p>   </p>
                        <p> 1.    </p>
                        <p> 2.    </p>
                        <span className="sblock">
                            <p className="sstitle" ><i> @Autowired </i>  </p>
                            <p>    </p>
                        </span>
                        <span className="sblock">
                            <p className="sstitle" ><i> @Qualifier </i>  </p>
                            <p>  ()    </p>
                        </span>
                        <span className="sblock">
                            <p className="sstitle" ><i>@Controller</i>  </p>
                            <p> </p>
                        </span>
                        <span className="sblock">
                            <p className="sstitle" ><i>@Service</i>  </p>
                            <p> </p>
                        </span>
                        <span className="sblock">
                            <p className="sstitle" ><i>@Repository</i>  </p>
                            <p> </p>
                        </span>
                        <span className="sblock">
                            <p className="sstitle" ><i>@Configuration</i>  </p>
                            <p> </p>
                        </span>
                        <span className="sblock">
                            <p className="sstitle" ><i>1.</i>  </p>
                            <p> </p>
                        </span>
                        <span className="sblock">
                            <p className="sstitle" ><i>1.</i>  </p>
                            <p> </p>
                        </span>
                        <span className="sblock">
                            <p className="sstitle" ><i>1.</i>  </p>
                            <p> </p>
                        </span>
                    </span>
                    <span className="mblock">
                        <span className="stitle">
                            <a name="xml  "> xml   </a>
                        </span>
                        <span className="sblock">
                            <p className="sstitle" ><i>xml</i>  </p>
                            <p>    , , x , , 
                            </p>
                            <p className="sstitle" ><i></i>  </p>
                            <p>  , o ,   
                            </p>
                        </span>
                    </span>
                    {/* <span className="mblock">
                        <span className="stitle">
                            <a name="">  </a>
                        </span>
                        <span className="sblock">
                            <p className="sstitle" ><i>1.</i>  </p>
                        </span>
                    </span> */}
                </span>
            </div>
        </>
    );
}

export default SpringCode;