import React from 'react';

const SpringList = (props) => {
    function send(page2) {
        props.onChangePage(page2)
    }

    return (
        <div className="springlist">
            <button onClick={() => { send('springlibrary') }}> Spring  </button>
            <button onClick={() => { send('springerror') }}> Spring  </button>
            <button onClick={() => { send('springcode') }}> Spring   </button>
            <button onClick={() => { send('springexcode1') }}> 1:Intellj,Gradle,JPA,MySQL </button>
            <button onClick={() => { send('springexcode2') }}> 2:Eclipse Bean Xml Injection </button>
            <button onClick={() => { send('springstudy') }}> Spring  </button>
            <button onClick={() => { send('springinitsetting') }}> Spring   </button>
        </div>
    );
}

export default SpringList;